.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background-color: #727d71;
  border-radius: 34px;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;

  background-color: white;
  transition: 0.4s;
}
input:checked + .slider:before {
  transform: translate(26px);
}
input:checked + .slider {
  background-color: #8cc78c;
}

.slider.rounded {
  /* border-radius: 34px; */
}
.slider.rounded:before {
  border-radius: 50%;
}
